.clients {
  &__wrapper {
    @apply flex flex-col items-center;
  }
  &__items {
    @apply gap-x-20 gap-y-[6.25rem] grid grid-cols-4 mt-[6.25rem] max-w-5xl;
  }
  &__item {
    img {
      @apply object-contain;
    }
    @apply max-w-[12.6875rem];
  }
  &__button {
    @apply mt-[5.625rem];
  }
  @apply pt-[6.875rem] pb-[10rem];
}