.case {
  &__image {
    img {
      @apply grayscale aspect-square transition-all duration-300;
    }
    @apply relative;
  }
  &__info {
    @apply flex justify-between items-center mt-5;
  }
  &__tags {
    @apply flex items-center gap-x-2;
  }
  &__tag {
    @apply px-3.5 py-1.5 border border-black-200 rounded-[99px] text-16-el text-black-200;
  }
  &__title {
    @apply text-30-l uppercase;
  }
  &__items {
    @apply absolute inset-0 flex items-end gap-x-8 bg-black-000 bg-opacity-70 opacity-0 px-7 pb-6 transition-all duration-300;
  }
  &__item {
    p {
      @apply font-normal text-40-el;
    }
    span {
      @apply font-normal text-20-el text-black-500;
    }
    @apply flex flex-col items-start gap-y-1;
  }
  @include screen(xl) {
    &:hover {
      img {
        @apply grayscale-0;
      }
      .case__items {
        @apply opacity-100;
      }
    }
  }
}