.footer {
  &__copy {
    p {
      a {
        @include screen(xl) {
          &:hover {
            @apply text-white-000;
          }
        }
        @apply ml-3 text-black-200 transition-all duration-300;
      }
      @apply text-14-el text-white-000;
    }
    @apply mt-10;
  }
  &__wrapper {
    @apply flex justify-between;
  }
  &__logo {
    @apply text-86-sb;
  }
  &__contacts {
    @apply flex items-center gap-x-4 mt-6;
  }
  &__icons {
    @apply flex gap-x-1.5;
  }
  &__address {
    @include screen(xl) {
      &:hover {
        @apply text-black-250;
      }
    }
    @apply not-italic transition-all duration-300;
  }
  &__menu {
    @apply flex gap-x-[5.9375rem] pt-3;
  }
  &__title {
    @apply text-20-l;
  }
  &__icon {
    svg {
      rect,
      path {
        @apply transition-all duration-300;
      }
    }
    @include screen(xl) {
      &:hover {
        svg {
          path {
            @apply stroke-black-000;
          }
          rect {
            @apply fill-white-000 stroke-white-000;
          }
        }
      }
    }
  }
  &__links {
    @apply flex flex-col flex-wrap gap-x-8 gap-y-4 mt-5 h-[7.5rem];
  }
  &__link {
    a {
      @include screen(xl) {
        &:hover {
          @apply text-black-250;
        }
      }
      @apply text-20-el transition-all duration-300;
    }
    span {
      @apply text-16-el text-black-200;
    }
    @apply flex flex-col items-start gap-y-1;
  }
  &__col {
    &-lg {
      @apply max-w-[28rem];
    }
  }
  @apply pt-12 pb-5;
}