.marquee {
  &__track {
    @apply flex w-max animate-marquee-scroll will-change-transform;
  }
  &__items {
    @apply flex;
  }
  &__item {
    @apply px-5 min-w-max max-w-max text-100-l text-blue-100 uppercase whitespace-nowrap; 
  }
  @apply z-10 flex items-center mt-20 w-full overflow-hidden;
}