.team {
  &__slider {
    @apply mt-14;
  }
  &__slide {
    img {
      @apply grayscale w-full transition-all duration-300;
    }
    h3 {
      @apply mt-4 text-30-l;
    }
    p {
      @apply mt-2 text-16-el;
    }
    @include screen(xl) {
      &:hover {
        img {
          @apply grayscale-0;
        }
      }
    }
  }
  @apply pt-20 pb-[6.25rem];
}