.services {
  .heading {
    &__title {
      span {
        @apply text-black-000;
      }
    }
    &__icon {
      svg {
        @apply text-black-000;
      }
    }
  }
  &__container {
    @apply grid grid-cols-2;
  }
  &__tag {
    @apply inline-flex bg-blue-100 mt-[3.75rem] px-4 py-2.5 rounded-[6.1875rem] text-16-l;
  }
  &__text {
    @apply mt-4 text-30-l text-black-000;
  }
  &__items {
    @apply flex flex-col mt-[3.125rem] max-w-[23.1875rem];
  }
  &__item {
    @apply px-4 py-5 border-t-2 border-black-000 text-26-l text-black-000;
  }
  @apply bg-white-000 pt-20 pb-80;
}