.header {
  &__wrapper {
    @apply flex justify-between items-center py-3.5;
  }
  &__logo {
    @apply text-28-sb;
  }
  &__button {
    span {
      @apply text-20-el;
    }
  }
  &__menu {
    @apply flex gap-x-12 absolute left-1/2 -translate-x-1/2;
  }
  &__link {
    @include screen(xl) {
      &:hover {
        @apply text-black-250;
      }
    }
    @apply text-20-el transition-all duration-300;
  }
  @apply top-0 z-50 fixed bg-black-100 bg-opacity-40 backdrop-blur-[0.4375rem] rounded-bl-[1.5625rem] rounded-br-[1.5625rem] w-full;
}