.button {
  &.with-border {
    @apply px-[22px] py-2.5 border-2 border-white-000 rounded-[99px];
  }
  span {
    @apply text-40-el transition-all duration-300;
  }
  &__icon {
    &.with-border {
      @apply border-2 border-white-000 rounded-full;
    }
    svg {
      @apply transition-all duration-300;
    }
    @apply box-content flex justify-center items-center w-9 min-w-9 max-w-9 h-9 min-h-9 max-h-9;
  }
  @include screen(xl) {
    &:hover {
      span {
        @apply text-black-000;
      }
      @apply bg-white-000 text-black-000;
    }
  }
  @apply inline-flex items-center gap-x-2.5 px-[26px] py-3 rounded-[99px] transition-all duration-300;
}