.heading {
  &__buttons {
    @apply flex flex-wrap justify-end gap-2 max-w-[19.625rem];
  }
  &__button {
    span {
      @apply text-16-el text-black-200;
    }
    &.active {
      span {
        @apply text-white-000;
      }
      @apply border-white-000;
    }
    &:hover {
      span {
        @apply text-black-000;
      }
    }
    @apply px-3.5 py-1.5 border border-black-200 rounded-[6.1875rem];
  }
  &__right {
    @apply flex gap-x-[3.75rem];
  }
  &__title {
    &-indent {
      span {
        @apply -ml-20 indent-20;
      }
    }
    svg {
      @apply mt-[2.125rem];
    }
    @apply flex gap-x-6 text-80-l;
  }
  &__text {
    @apply max-w-[38.8125rem] text-30-l;
  }
  &-rows {
    .heading__text {
      @apply max-w-[82.75rem] text-26-l;
    }
    .heading__icon {
      @apply top-0 right-0 absolute;
    }
    @apply relative flex-col items-start;
  }
  @apply flex justify-between items-center gap-y-3 pb-6 w-full;
}