* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30px white inset !important;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
  appearance: textfield;
}

textarea,
input,
button,
select,
a {
  @apply rounded-none outline-none font-sans font-normal touch-manipulation;
}

button {
  @apply select-none;
}

html,
body {
  @apply bg-black-000 font-sans text-[0.94vw] text-white-000 2xl:text-[1vw];
}

.icons {
  @apply hidden;
}

.blur {
  @apply blur-[10px] pointer-events-none;
}

.container {
  @apply mx-auto;
}

.render {
  @apply top-0 left-0 z-[99999] fixed bg-black-000 w-full h-full;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}