.hero {
  &__wrapper {
    @apply relative flex justify-between;
  }
  &__tags {
    @apply flex items-center gap-x-3;
  }
  &__tag {
    @apply bg-black-100 px-4 py-3 rounded-[6.1875rem] text-16-l;
  }
  &__title {
    @apply flex flex-col items-start mt-8;
  }
  &__name {
    span {
      @apply z-20 relative;
    }
    &:before {
      @apply bottom-2.5 left-0 absolute bg-blue-000 w-full h-5 content-[''];
    }
    @apply inline-block relative text-110-l uppercase;
  }
  &__subname {
    @apply text-110-l;
  }
  &__image {
    img {
      @apply min-w-[260px] 2xl:min-w-[341px] max-w-[260px] 2xl:max-w-[341px];
    }
    @apply right-[8.4375rem] absolute;
  }
  &__button {
    @include screen(xl) {
      &:hover {
        svg {
          @apply -rotate-[125deg];
        }
      }
    }
    @apply mt-6 -ml-6;
  }
  &__footer {
    @apply flex justify-between items-end mt-[1.5625rem] 2xl:mt-4;
  }
  &__trigger {
    img {
      @apply absolute w-full max-w-[10.375rem] h-full max-h-[10.375rem] animate-spin-slow;
    }
    @apply flex justify-center items-center w-[10.375rem] max-w-[10.375rem] h-[10.375rem] max-h-[10.375rem];
  }
  &__arrow {
    &:before {
      @apply absolute border-2 border-white-000 rounded-full w-[2.875rem] h-[2.875rem] content-[''];
    }
    &:after {
      @apply absolute border-2 border-white-000 rounded-full w-[3.75rem] h-[3.75rem] content-[''];
    }
    @apply relative flex justify-center items-center;
  }
  &__award {
    @apply max-w-[15.9375rem] max-h-[15.25rem];
  }
  @apply bg-black-000 pt-[7.5rem] min-h-dvh;
}