.about {
  &__slogan {
    span {
      &.blue {
        @apply text-blue-000;
      }
      &:nth-child(1) {
        @apply pr-[9.375rem];
      }
      @apply text-110-l;
    }
    @apply flex flex-col;
  }
  &__items {
    @apply flex justify-between mt-[8.75rem];
  }
  &__item {
    p {
      i {
        @apply not-italic;
      }
      @apply text-160-l;
    }
    span {
      @apply text-60-l;
    }
    @apply flex flex-col justify-center items-center col-span-1;
  }
  @apply py-[6.5625rem];
}